import service from "./service";

export default {
  namespaced: true,

  state: {
    discover: [],
    loading: true,
    movieLoader: true,
    singleMovie: {},
    page: "",
    total_pages: ""
  },

  mutations: {
    SET_DISCOVER(state, results) {
      state.discover = results.results;
      state.page = results.page;
      state.total_pages = results.total_pages;
    },

    SET_SINGLE_MOVIE(state, result) {
      state.singleMovie = result;
      state.movieLoader = false;
    },

    SET_SINGLE_LOADER(state) {
      state.movieLoader = true;
    }
  },

  actions: {
    async fetchDiscover({ commit, dispatch }) {
      try {
        let response = await service.fetchDiscoverService();
        if (Object.keys(response.data.results).length > 0) {
          commit("SET_DISCOVER", response.data);
          setTimeout(() => {
            dispatch("contentLoaderHide", null, { root: true });
          }, 1000);
        }
      } catch (err) {
        console.log(err);
      }
    },
    async fetchDiscoverMore({ commit, state, dispatch }) {
      try {
        if (state.page < state.total_pages) {
          let response = await service.fetchDiscoverMore(state.page + 1);
          if (Object.keys(response.data.results).length > 0) {
            commit("SET_DISCOVER", response.data);
            setTimeout(() => {
              dispatch("contentLoaderHide", null, { root: true });
            }, 1000);
            window.scroll({
              top: 0,
              left: 0,
              behavior: "smooth"
            });
          }
        }
      } catch (err) {
        console.log(err);
      }
    },

    async fetchDiscoverPrev({ commit, state, dispatch }) {
      try {
        if (state.page < state.total_pages) {
          let response = await service.fetchDiscoverPrev(state.page - 1);
          if (Object.keys(response.data.results).length > 0) {
            commit("SET_DISCOVER", response.data);
            setTimeout(() => {
              dispatch("contentLoaderHide", null, { root: true });
            }, 1000);
            window.scroll({
              top: 0,
              left: 0,
              behavior: "smooth"
            });
          }
        }
      } catch (err) {
        console.log(err);
      }
    },

    async getDetail({ commit }, id = 0) {
      commit("SET_SINGLE_LOADER");
      try {
        let response = await service.getDetailService(id);
        if (response && response.status === 200) {
          commit("SET_SINGLE_MOVIE", response.data);
        } else {
          console.log("muur");
        }
      } catch (err) {
        console.log(err);
      }
    }
  },

  getters: {
    getLoading(state) {
      return state.loading;
    },
    getMovieLoader(state) {
      return state.movieLoader;
    },
    getCurrentPage(state) {
      return state.page;
    },
    getTotalPage(state) {
      return state.total_pages;
    }
  }
};
