import Api from '@/api'

export default {
    fetchSearch(value) {
        return Api().get(`search/movie?api_key=${process.env.VUE_APP_API_KEY}&query=${value}`)
    },
    fetchSearchMore(value, page) {
        return Api().get(`search/movie?api_key=${process.env.VUE_APP_API_KEY}&query=${value}&page=${page}`)
    },
    fetchSearchPrev(value, page) {
        return Api().get(`search/movie?api_key=${process.env.VUE_APP_API_KEY}&query=${value}&page=${page}`)
    },

}