import service from './service';

export default{
  namespaced: true,

  state: {
    SITE_LOGO : require('@/assets/logo.png'),
    genres: [],
    loading: true,
  },

  mutations: {

    SET_GENRES (state,genres) {
      state.genres = genres;
      setTimeout(() => {
        state.loading = false;
      }, 1000);
    }

  },

  actions: {

    async fetchGenres({commit}) {
      try{
        let response = await service.initGenres();
        if(Object.keys(response.data.genres).length > 0) {
          commit('SET_GENRES',response.data.genres);
        }
      }catch(err){
        console.log(err)
      }
    }

  },

  getters:{

    getGenres:(state) => {
      return state.genres;
    }

  }

}
