import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex);

//import module
import menu from './menu/menu';
import movie from './movie/movie';
import genres from './genres/genres';
import search from './search/search';

export default new Vuex.Store({

  modules: {
    menu, movie, genres, search
  },

  state: {
    contentLoader: true,
  },

  mutations: {
    SET_SHOW_CONTENTLOADER(state) {
      state.contentLoader = true;
    },
    SET_HIDE_CONTENTLOADER(state) {
      state.contentLoader = false;
    },
  },

  actions: {
    contentLoaderShow({ commit, state }) {
      commit('SET_SHOW_CONTENTLOADER', state);
    },
    contentLoaderHide({ commit, state }) {
      commit('SET_HIDE_CONTENTLOADER', state);
    },
  },
  getters: {
    contentLoaderMain(state) {
      return state.contentLoader;
    }
  }


})
