import service from "./service";

export default {
  namespaced: true,

  state: {
    result: {},
    searchValue: "",
    page: 0,
    total_pages: 0
  },

  mutations: {
    SET_RESULT(state, response) {
      state.result = response.results.results;
      state.page = response.results.page;
      state.total_pages = response.results.total_pages;
      state.searchValue = response.value;
    },
    SET_SEARCH_VALUE(state, value) {
      state.result = {};
      state.searchValue = value;
    }
  },

  actions: {
    async fetchSearch({ commit }, searchValue) {
      if (searchValue.value.length > 0) {
        try {
          let response = await service.fetchSearch(searchValue.value);
          if (Object.keys(response.data.results).length > 0) {
            commit("SET_RESULT", {
              results: response.data,
              value: searchValue.value
            });
          } else {
            commit("SET_SEARCH_VALUE", searchValue.value);
          }
        } catch (err) {
          console.log(err);
        }
      }
    },

    async fetchSearchMore({ commit, state }, searchValue) {
      if (searchValue.value.length > 0) {
        try {
          if (state.page < state.total_pages) {
            let response = await service.fetchSearchMore(
              searchValue.value,
              state.page + 1
            );
            if (Object.keys(response.data.results).length > 0) {
              commit("SET_RESULT", {
                results: response.data,
                value: searchValue.value
              });
              window.scroll({
                top: 0,
                left: 0,
                behavior: "smooth"
              });
            }
          }
        } catch (err) {
          console.log(err);
        }
      }
    },

    async fetchSearchPrev({ commit, state }, searchValue) {
      if (searchValue.value.length > 0) {
        try {
          if (state.page < state.total_pages) {
            let response = await service.fetchSearchPrev(
              searchValue.value,
              state.page - 1
            );
            if (Object.keys(response.data.results).length > 0) {
              commit("SET_RESULT", {
                results: response.data,
                value: searchValue.value
              });
              window.scroll({
                top: 0,
                left: 0,
                behavior: "smooth"
              });
            }
          }
        } catch (err) {
          console.log(err);
        }
      }
    }
  },

  getters: {
    getResult(state) {
      return state.result;
    },
    getSearchValue(state) {
      return state.searchValue;
    },
    getCurrentPage(state) {
      return state.page;
    },
    getTotalPage(state) {
      return state.total_pages;
    }
  }
};
