import Api from '@/api'

export default {
    fetchGenresService(selectedGenres) {
        return Api().get(`discover/movie?api_key=${process.env.VUE_APP_API_KEY}&with_genres=${selectedGenres.id}&sort_by=popularity.desc`)
    },
    fetchGenresMore(selectedGenres, page) {
        return Api().get(`discover/movie?api_key=${process.env.VUE_APP_API_KEY}&with_genres=${selectedGenres.id}&page=${page}&sort_by=popularity.desc`)
    },
    fetchGenresPrev(selectedGenres, page) {
        return Api().get(`discover/movie?api_key=${process.env.VUE_APP_API_KEY}&with_genres=${selectedGenres.id}&page=${page}&sort_by=popularity.desc`)
    }
}