import router from "../../router";
import service from "./service";

export default {
  namespaced: true,
  state: {
    genres: [],
    loadingGenre: true,
    UserSelectedGenres: "",
    page: "",
    total_pages: ""
  },

  mutations: {
    SET_GENRES(state, results) {
      state.UserSelectedGenres = results.slug.slug;
      state.genres = results.results.results;
      state.page = results.results.page;
      state.total_pages = results.results.total_pages;
    },

    SET_LOADING(state, value) {
      state.loadingGenre = value;
    }
  },

  actions: {
    async fetchGenres({ commit, rootState }, slug) {
      commit("SET_LOADING", true);
      let selectedGenres = rootState.menu.genres.find(e => {
        return e.name === slug.slug;
      });
      if (selectedGenres) {
        try {
          let response = await service.fetchGenresService(selectedGenres);
          if (Object.keys(response.data.results).length > 0) {
            let results = response.data;
            commit("SET_GENRES", { results, slug });
            commit("SET_LOADING", false);
          }
        } catch (err) {
          console.log(err);
        }
      } else {
        router.push({ path: "/" });
      }
    },

    async fetchGenresMore({ commit, rootState, state }, slug) {
      commit("SET_LOADING", true);
      let selectedGenres = rootState.menu.genres.find(e => {
        return e.name === slug.slug;
      });
      if (selectedGenres) {
        try {
          if (state.page < state.total_pages) {
            let response = await service.fetchGenresMore(
              selectedGenres,
              state.page + 1
            );
            if (Object.keys(response.data.results).length > 0) {
              let results = response.data;
              commit("SET_GENRES", { results, slug });
              commit("SET_LOADING", false);
              window.scroll({
                top: 0,
                left: 0,
                behavior: "smooth"
              });
            }
          }
        } catch (err) {
          console.log(err);
        }
      } else {
        router.push({ path: "/" });
      }
    },

    async fetchGenresPrev({ commit, rootState, state }, slug) {
      commit("SET_LOADING", true);
      let selectedGenres = rootState.menu.genres.find(e => {
        return e.name === slug.slug;
      });
      if (selectedGenres) {
        try {
          if (state.page < state.total_pages) {
            let response = await service.fetchGenresMore(
              selectedGenres,
              state.page - 1
            );
            if (Object.keys(response.data.results).length > 0) {
              let results = response.data;
              commit("SET_GENRES", { results, slug });
              commit("SET_LOADING", false);
              window.scroll({
                top: 0,
                left: 0,
                behavior: "smooth"
              });
            }
          }
        } catch (err) {
          console.log(err);
        }
      } else {
        router.push({ path: "/" });
      }
    }
  },

  getters: {
    getGenres(state) {
      return state.genres;
    },
    getLoading(state) {
      return state.loadingGenre;
    },
    getUserSelectedGenres(state) {
      return state.UserSelectedGenres;
    },
    getCurrentPage(state) {
      return state.page;
    },
    getTotalPage(state) {
      return state.total_pages;
    }
  }
};
