import Api from '@/api'

export default {
    fetchDiscoverService() {
        return Api().get(`discover/movie?api_key=${process.env.VUE_APP_API_KEY}&language=en-US&sort_by=popularity.desc&include_adult=false&include_video=false&page=1`)
    },
    getDetailService(id) {
        return Api().get(`movie/${id.id}?api_key=${process.env.VUE_APP_API_KEY}&append_to_response=videos`)
    },
    fetchDiscoverMore(page) {
        return Api().get(`discover/movie?api_key=${process.env.VUE_APP_API_KEY}&language=en-US&sort_by=popularity.desc&include_adult=false&include_video=false&page=${page}`)
    },
    fetchDiscoverPrev(page) {
        return Api().get(`discover/movie?api_key=${process.env.VUE_APP_API_KEY}&language=en-US&sort_by=popularity.desc&include_adult=false&include_video=false&page=${page}`)
    }
}